import React, { Component } from 'react';
import { Layout, Menu, Button, Spin } from 'antd';
import { Route, Link } from 'react-router-dom';

import Post from '../post/';
import github from '../../lib/github';

const { Sider, Content } = Layout;

const styles = {
  content: {
    background: '#fff',
    padding: 24,
    margin: 0,
    minHeight: 280
  }
};

class Posts extends Component {
  state = {
    postsPage: 1,
    maxPostsPage: null,
    posts: [],
    loading: false
  };

  async componentWillMount() {
    await this.getPosts(this.state.postsPage);
  }

  setStateAsync(newState) {
    return new Promise(resolve => {
      this.setState(newState, () => {
        resolve();
      });
    });
  }

  async getPosts(page) {
    let posts = this.state.posts;
    try {
      this.setStateAsync({ loading: true });
      const res = await github.get(`/repos/axetroy/blog/issues?page=${page}`);

      // if get the last page
      if ((res.data && res.data.length === 0) || res.data.length < 30)
        this.setState({ maxPostsPage: page });

      if (res.data && res.data.length) {
        posts = [].concat(posts).concat(res.data || []);

        this.setState({
          postsPage: page,
          posts,
          loading: false
        });
      }
    } catch (err) {
      console.error(err);
    }
    return posts;
  }

  render() {
    return (
      <Layout>
        <Sider width={250} style={styles.content}>
          <Spin spinning={this.state.loading}>

            <Menu mode="inline" style={{ height: '100%' }}>

              {this.state.posts.map((post, index) => {
                return (
                  <Menu.Item key={index}>
                    <Link to={`/post/${post.number}`} title={post.title}>
                      {post.title}
                    </Link>
                  </Menu.Item>
                );
              })}

              {this.state.maxPostsPage === this.state.postsPage ||
                this.state.loading
                ? ''
                : <Menu.Item>
                    <Button
                      type="primary"
                      loading={this.state.loading}
                      onClick={() => this.getPosts(this.state.postsPage + 1)}
                    >
                      {this.state.loading ? 'Loading...' : 'More'}
                    </Button>
                  </Menu.Item>}

            </Menu>
          </Spin>
        </Sider>

        <Content style={styles.content}>
          <Route path="/post/:number" component={Post} />
        </Content>
      </Layout>
    );
  }
}

export default Posts;



