import React, { Component } from 'react';
import GitHubCalendar from 'github-calendar';

import { Row, Col, Card, Spin } from 'antd';

import github from '../../lib/github';

import './index.css';

const styles = {
  infoBlock: {
    width: '80%',
    margin: '0 auto',
    textAlign: 'center',
    color: '#fff',
    padding: 20
  },
  strong: { fontSize: '2em' }
};

class Github extends Component {
  state = { user: {}, profileLoading: false };

  componentDidMount() {
    GitHubCalendar('.calendar', 'axetroy');
  }

  setStateAsync(newState) {
    return new Promise(resolve => {
      this.setState(newState, () => {
        resolve();
      });
    });
  }

  async componentWillMount() {
    let user = {};
    try {
      await this.setStateAsync({ profileLoading: true });
      const response = await github.get('/users/axetroy');
      user = response.data;
    } catch (err) {
      console.error(err);
    }
    this.setState({ user, profileLoading: false });
    return user;
  }

  render() {
    return (
      <Row style={{ width: 750, margin: '0 auto' }}>

        <h2 className="github-title">
          
        </h2>

        <div className="calendar" />

        <h2 className="github-title">
          
        </h2>

        <Card style={{ width: '100%' }}>
          <Spin spinning={this.state.profileLoading}>
            <Row>
              <Col span={4}>
                <a href={this.state.user.html_url} target="_blank">
                  <img
                    alt={this.state.user.avatar_url}
                    style={{
                      width: '70%',
                      height: 'auto',
                      borderRadius: '50%'
                    }}
                    src={this.state.user.avatar_url}
                  />
                </a>
              </Col>
              <Col span={20}>
                <p>{this.state.user.name}</p>
                <p>{this.state.user.created_at}</p>
                <p>
                  
                  {this.state.user.created_at
                    ? ((new Date() - new Date(this.state.user.created_at)) /
                        1000 /
                        3600 /
                        24 /
                        365).toFixed(1)
                    : ''}
                  
                </p>
                <blockquote>
                  {this.state.user.bio}
                </blockquote>
                <p>
                  <span>Repositories {this.state.user.public_repos}</span>
                  <span>Followers {this.state.user.followers}</span>
                </p>
              </Col>
            </Row>

            <hr className="hr" />

            <Row>
              <Col span={8}>
                <div className="bg-green" style={styles.infoBlock}>
                  <span style={styles.strong}>
                    {this.state.user.public_repos}
                  </span>
                  {' '}
                  Repositories
                </div>
              </Col>
              <Col span={8}>
                <div className="bg-green" style={styles.infoBlock}>
                  <span style={styles.strong}>
                    {this.state.user.followers}
                  </span>
                  {' '}
                  Followers
                </div>
              </Col>
              <Col span={8}>
                <div className="bg-green" style={styles.infoBlock}>
                  <span style={styles.strong}>
                    {this.state.user.following}
                  </span>
                  {' '}
                  Following
                </div>
              </Col>
            </Row>

          </Spin>
        </Card>

        <h2 className="github-title"></h2>
        <Card>
          <div>
            Repositories
          </div>
        </Card>

      </Row>
    );
  }
}

export default Github;



