import React, { Component } from 'react';
import { Layout, Card, Row, Col, Button } from 'antd';
import { Link } from 'react-router-dom';

import github from '../../lib/github';

const { Content } = Layout;

class Repos extends Component {
  state = {
    reposPage: 1,
    maxReposPage: null,
    repos: [],
    loading: false
  };

  async componentWillMount() {
    await this.getRepos(this.state.reposPage);
  }

  setStateAsync(newState) {
    return new Promise(resolve => {
      this.setState(newState, () => {
        resolve();
      });
    });
  }

  async getRepos(page) {
    if (this.state.loading) return;

    let repos = this.state.repos || [];

    let res;
    try {
      await this.setStateAsync({ loading: true });
      res = await github.get(`/users/axetroy/repos`, { params: { page } });

      // get the last page
      if ((res.data && res.data.length === 0) || res.data.length < 30) {
        this.setState({ maxReposPage: page });
      }

      repos = []
        .concat(this.state.repos)
        .concat(res.data.filter(repo => !repo.fork));

      await this.setStateAsync({
        reposPage: page,
        repos,
        loading: false
      });
    } catch (err) {
      console.error(err);
    }
    return repos;
  }

  render() {
    return (
      <Layout>
        <Content
          style={{
            background: '#fff',
            padding: 24,
            margin: 0,
            minHeight: 280
          }}
        >
          {this.state.repos.map((repo, index) => {
            return (
              <Card
                key={index}
                title={repo.name}
                extra={
                  <Link to={`/repo/${repo.owner.login}/${repo.name}`}>
                    More
                  </Link>
                }
                style={{
                  width: 240,
                  minHeight: 300,
                  display: 'inline-block',
                  margin: '10px'
                }}
              >
                <p>{repo.description}</p>
              </Card>
            );
          })}

          {this.state.maxReposPage === this.state.reposPage
            ? ''
            : <Row>
                <Col className="text-center">
                  <Button
                    type="primary"
                    loading={this.state.loading}
                    onClick={() => this.getRepos(this.state.reposPage + 1)}
                  >
                    {this.state.loading ? 'Loading...' : 'More'}
                  </Button>
                </Col>
              </Row>}
        </Content>
      </Layout>
    );
  }
}

export default Repos;



