import React, { Component } from 'react';

import { Row, Col, Icon } from 'antd';

class Github extends Component {
  state = {
    date: new Date()
  };

  componentDidMount() {
    const intervalId = setInterval(this.timer.bind(this), 1000);
    this.setState({ intervalId: intervalId });
  }

  componentWillUnmount() {
    clearInterval(this.state.intervalId);
  }

  timer() {
    // setState method is used to update the state
    this.setState({ date: new Date() });
  }

  render() {
    return (
      <Row className="text-center">
        <Col span={8}>
          <h3><Icon type="github" />Github</h3>
          <div>
            <a target="_blank" href="https://github.com/axetroy/my"></a>
          </div>
        </Col>
        <Col span={8}>
          <h3><Icon type="link" /></h3>
          <div>
            <a target="_blank" href="https://facebook.github.io/react/">
              React
            </a>
          </div>
          <div><a target="_blank" href="https://ant.design">Antd</a></div>
        </Col>
        <Col span={8}>
          <p>Copyright  2017</p>
          <p>
            Created by
            {' '}
            <a target="_blank" href="https://github.com/axetroy">Axetroy</a>
          </p>
          <p>{this.state.date.toString()}</p>
        </Col>
      </Row>
    );
  }
}

export default Github;



