import React, { Component } from 'react';
import ReactMarkdown from 'react-markdown';
import { Spin } from 'antd';

import github from '../../lib/github';

class Post extends Component {
  state = { post: {}, loading: false };

  async componentWillMount() {
    const number = this.props.match.params.number;
    await this.getPost(number);
  }

  async componentWillReceiveProps(nextProp) {
    await this.getPost(nextProp.match.params.number);
  }

  setStateAsync(newState) {
    return new Promise(resolve => {
      this.setState(newState, () => {
        resolve();
      });
    });
  }

  async getPost(number) {
    try {
      await this.setStateAsync({ loading: true });
      const res = await github.get(`/repos/axetroy/blog/issues/${number}`);
      this.setState({ post: res.data || {}, loading: false });
      return res.data;
    } catch (err) {
      console.error(err);
    }
  }

  render() {
    return (
      <Spin spinning={this.state.loading}>
        <h2>{this.state.post.title}</h2>
        <ReactMarkdown
          className="markdown-body"
          source={this.state.post.body || ''}
        />
      </Spin>
    );
  }
}

export default Post;



