import React, { Component } from 'react';
import {
  HashRouter as Router,
  Route,
  Link,
  Switch,
  HashRouter
} from 'react-router-dom';

import { Layout, Menu } from 'antd';

const { Header, Content, Footer } = Layout;

import Home from './pages/home';
import Github from './pages/github';
import About from './pages/about';
import Posts from './pages/posts';
import Repos from './pages/repos';
import Repo from './pages/repo';

import GlobalFooter from './component/footer';

import './App.css';

class App extends Component {
  state = {
    nav: [
      {
        name: 'Home',
        title: '',
        href: '/'
      },
      {
        name: 'post',
        title: '',
        href: '/post'
      },
      {
        name: 'repo',
        title: '',
        href: '/repo'
      },
      {
        name: 'github',
        title: 'Github',
        href: '/github'
      },
      {
        name: 'about',
        title: '',
        href: '/about'
      }
    ]
  };

  render() {
    return (
      <Router history={HashRouter}>
        <Layout>
          <a target="_blank" href="https://github.com/axetroy/my">
            <img
              style={{ position: 'absolute', top: 0, right: 0, border: 0 }}
              src="https://camo.githubusercontent.com/e7bbb0521b397edbd5fe43e7f760759336b5e05f/68747470733a2f2f73332e616d617a6f6e6177732e636f6d2f6769746875622f726962626f6e732f666f726b6d655f72696768745f677265656e5f3030373230302e706e67"
              alt="Fork me on GitHub"
              data-canonical-src="https://s3.amazonaws.com/github/ribbons/forkme_right_green_007200.png"
            />
          </a>
          <Header className="header">
            <div className="logo" style={{ opacity: 0 }} />
            <Menu
              theme="dark"
              mode="horizontal"
              defaultSelectedKeys={['2']}
              style={{ lineHeight: '64px' }}
            >
              {this.state.nav.map(nav => {
                return (
                  <Menu.Item key={nav.name}>
                    <Link to={nav.href}>{nav.title}</Link>
                  </Menu.Item>
                );
              })}
            </Menu>
          </Header>
          <Layout>
            <Layout style={{ padding: '0 24px 24px' }}>
              <Content
                style={{
                  background: '#fff',
                  padding: 24,
                  margin: 0,
                  minHeight: 280
                }}
              >
                <Switch>
                  <Route exact path="/" component={Home} />
                  <Route exact path="/github" component={Github} />
                  <Route exact path="/about" component={About} />
                  <Route path="/post" component={Posts} />
                  <Route exact path="/repo" component={Repos} />
                  <Route path="/repo/:owner/:repo" component={Repo} />
                </Switch>

              </Content>
              <Footer>
                <GlobalFooter />
              </Footer>
            </Layout>
          </Layout>
        </Layout>
      </Router>
    );
  }
}

export default App;



