import React, { Component } from 'react';
import ReactMarkdown from 'react-markdown';
import { Layout, Spin, Row, Col } from 'antd';

const { Content, Sider } = Layout;

import http from 'axios';
import github from '../../lib/github';

class Repo extends Component {
  state = { repo: {}, readme: '' };

  async componentWillMount() {
    await this.getData(this.props);
  }

  async componentWillReceiveProps(nextProp) {
    const { nextOwner, nextRepo } = nextProp.match.params;
    const { owner, repo } = this.props.match.params;
    if (nextOwner !== owner && nextRepo !== repo) {
      await this.getData(nextProp);
    }
  }

  setStateAsync(newState) {
    return new Promise(resolve => {
      this.setState(newState, () => {
        resolve();
      });
    });
  }

  async getData(props) {
    if (this.state.loading) return;

    await this.setStateAsync({ loading: true });

    const { owner, repo } = props.match.params;
    const data = await this.getRepo(owner, repo);
    await this.getReadMe(owner, repo, data.default_branch);
    await this.setStateAsync({ loading: false });
  }

  async getRepo(owner, repo) {
    let data = null;
    try {
      const res = await github.get(`/repos/${owner}/${repo}`);
      data = res.data;
      this.setState({ repo: data || {} });
    } catch (err) {
      console.error(err);
      return null;
    }
    return data;
  }

  async getReadMe(owner, repo, branch) {
    let raw = '';
    try {
      const res = await http.get(
        `https://raw.githubusercontent.com/${owner}/${repo}/${branch || 'master'}/README.md`
      );
      raw = res.data;
    } catch (err) {
      raw = '';
      console.error(err);
    }
    this.setState({ readme: raw });
    return raw;
  }

  render() {
    return (
      <Row>
        <Col span={18} offset={3}>

          <Layout>
            <Sider width={250} style={{ background: '#fff' }}>
              <h1>
                <a target="_blank" href={this.state.repo.html_url}>
                  {this.state.repo.name}
                </a>
              </h1>
              <p>Watch {this.state.repo.watchers_count}</p>
              <p>Start {this.state.repo.stargazers_count}</p>
              <p>Fork {this.state.repo.forks_count}</p>
            </Sider>

            <Content
              style={{
                background: '#fff',
                padding: '0 24px',
                margin: 0,
                minHeight: 280
              }}
            >
              <Spin spinning={!!this.state.loading} delay={0} tip="Loading...">
                <ReactMarkdown
                  className="markdown-body"
                  styles={{ fontSize: '16px' }}
                  source={this.state.readme || ''}
                />
              </Spin>
            </Content>
          </Layout>

        </Col>
      </Row>
    );
  }
}

export default Repo;



